import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import ProjectView from '@/components/ProjectView'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/project',
      name: 'ProjectView',
      component: ProjectView,
    }
  ]
})



